/*----------------------------------------*/
/*   preloader                            */
/*----------------------------------------*/
.loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000;
}
.loader-wrapper .loader-section {
  position: fixed;
  top: 0;
  background: var(--color-white);
  width: 50%;
  height: 100%;
  z-index: 1000;
}
.loader-wrapper .loader-section.section-left {
  left: 0;
}
.loader-wrapper .loader-section.section-right {
  right: 0;
}

.loaded .loader-wrapper .loader-section.section-left, .loaded .loader-wrapper .loader-section.section-right {
  transition: 0.7s cubic-bezier(0.645, 0.045, 0.355, 1) 0.3s;
  opacity: 0;
}
.loaded .loader-wrapper {
  visibility: hidden;
  transform: translateY(-100%);
  transition: 0.3s ease-out 1s;
}
.loaded .loader {
  opacity: 0;
  transition: 0.3s ease-out;
}

.loader {
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  z-index: 1001;
  transform: translate(-50%, -50%);
}
.loader::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 14px;
  right: 14px;
  bottom: 14px;
  border: 4px solid transparent;
  border-top-color: var(--color-primary);
  border-right-color: var(--color-primary);
  border-bottom-color: var(--color-primary);
  border-radius: 100%;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes spin {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}
.bg-black {
  background: var(--color-heading) !important;
}

.cw {
  color: var(--color-white) !important;
}

.rts-back-to-top {
  position: fixed;
  right: 15px;
  bottom: 15px;
  width: 55px;
  height: 55px;
  background: var(--color-primary);
  color: var(--color-white);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: var(--color-white);
  z-index: 9999;
  opacity: 0;
  cursor: pointer;
  z-index: 9;
  transition: all 0.3s ease;
}
.rts-back-to-top.active {
  opacity: 1;
}

.page-nav ul li {
  display: inline-block;
}
.page-nav ul li.active a {
  background: var(--color-primary);
  color: var(--color-white);
  border-color: transparent;
}
.page-nav ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: var(--color-white);
  border: 1px solid #E0E4ED;
  border-radius: 15px;
  color: var(--color-heading);
  font-weight: var(--f-bold);
  margin-right: 10px;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.page-nav ul li a:hover {
  background: var(--color-primary);
  color: var(--color-white);
  border-color: transparent;
}

