/*----------------------------------------*/
/*   BUTTON STYLES
/*----------------------------------------*/
.rts-btn {
  display: inline-block;
  font-size: 16px;
  font-family: var(--font-primary);
  font-weight: var(--f-semi-bold);
  line-height: 26px;
  padding: 15px 36px;
  border-radius: 5px;
  max-width: max-content;
  width: max-content;
  height: max-content;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.rts-btn i {
  margin-left: 8px;
  transform: rotate(-45deg);
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.rts-btn.btn-primary {
  background: var(--color-primary);
  color: var(--color-white);
}
.rts-btn.btn-primary:hover {
  background: var(--color-heading);
}
.rts-btn.btn-primary:hover i {
  transform: rotate(0deg);
}
.rts-btn.btn-primary-2 {
  padding: 10px 25px;
  background: transparent;
  color: var(--color-primary);
  border: 1px solid #E6E6EB;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.rts-btn.btn-primary-2 i {
  transform: rotate(-45deg);
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.rts-btn.btn-primary-2:hover {
  background: var(--color-primary);
  color: var(--color-white);
}
.rts-btn.text-btn {
  padding: 0;
  background: transparent;
  color: var(--color-heading);
  border: none;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.rts-btn.text-btn i {
  margin-left: 5px;
  transform: rotate(-45deg);
  margin-bottom: -5px;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}
.rts-btn.text-btn:hover {
  background: transparent;
  color: var(--color-primary);
}
.rts-btn.text-btn:hover i {
  transform: rotate(0deg);
}

