@mixin mq($point) {
  @if $point==xsmall {
    @media (min-width: 450px) {
      @content ;
    }
  }

  @else if $point==max-xxsmall {
    @media (max-width:450px) {
      @content ;
    }
  }

  @if $point==xxs {
    @media (min-width: 470px) {
      @content ;
    }
  }

  @else if $point==max-xxs {
    @media (max-width:470px) {
      @content ;
    }
  }

  @if $point==xs {
    @media (min-width: 500px) {
      @content ;
    }
  }

  @else if $point==max-xs {
    @media (max-width: 500px) {
      @content ;
    }
  }

  @if $point==sm {
    @media (min-width: 576px) {
      @content ;
    }
  }

  @else if $point==max-sm {
    @media (max-width: 575px) {
      @content ;
    }
  }

  @else if $point==md {
    @media (min-width: 768px) {
      @content ;
    }
  }

  @else if $point==max-md {
    @media (max-width: 767px) {
      @content ;
    }
  }

  @else if $point==lg {
    @media (min-width: 992px) {
      @content ;
    }
  }

  @else if $point==max-lg {
    @media (max-width: 991px) {
      @content ;
    }
  }

  @else if $point==xl {
    @media (min-width: 1200px) {
      @content ;
    }
  }

  @else if $point==max-xl {
    @media (max-width: 1199px) {
      @content ;
    }
  }

  @else if $point==xxl {
    @media (min-width: 1400px) {
      @content ;
    }
  }

  @else if $point==max-xxl {
    @media (max-width: 1399px) {
      @content ;
    }
  }

  @else if $point==max-xxl2 {
    @media (max-width: 1440px) {
      @content ;
    }
  }

  @else if $point==xxxl {
    @media (min-width: 1600px) {
      @content ;
    }
  }

  @else if $point==max-xxxl {
    @media (max-width: 1600px) {
      @content ;
    }
  }

  @else if $point==xl4 {
    @media (min-width: 1840px) {
      @content ;
    }
  }

  @else if $point==max-xl4 {
    @media (max-width: 1839px) {
      @content ;
    }
  }
}

@mixin before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  content: "";
}

@mixin after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  content: "";
}


@mixin flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin flex-center {
  display: flex;
  align-items: center;
}

@mixin flex-between {
  display: flex;
  justify-content: space-between;
}

@mixin flex-col($gap) {
  display: flex;
  flex-direction: column;
  gap: $gap;
}

@mixin rts-font-member($size, $ff, $fw) {
  font-family: $ff;
  font-style: normal;
  font-weight: $fw;
  font-size: $size;
}

@mixin rts-transition($time: all 0.4s) {
  -webkit-transition: $time;
  -o-transition: $time;
  transition: $time;
}

// placeholder input
@mixin rts-placeholder {
  &::-webkit-input-placeholder {
    @content;
  }

  &:-moz-placeholder {
    @content;
  }

  &::-moz-placeholder {
    @content;
  }

  &:-ms-input-placeholder {
    @content;
  }
}